<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <b-row class="justify-content-center">
        <b-col md="6" sm="8">
          <b-card no-body class="mx-4">
            <b-card-body class="p-4">
              <b-form>
                <h1>Register</h1>
                <p class="text-muted">Create your account</p>
                <div class="alert alert-danger" v-show="!!serverError">{{ serverError }}</div>
                <b-input-group class="mb-3" >
                    <b-input-group-prepend>
                      <b-input-group-text>@</b-input-group-text>
                    </b-input-group-prepend>
                    <b-form-input v-validate="'required|email'" type="text" placeholder="Email" autocomplete="email" v-model="email" id="email" name="email" :class="{'is-invalid': errors.has('email') }" />
                </b-input-group>
                <div class="alert alert-danger" v-show="errors.has('email')">{{ errors.first('email')}}</div>

                <b-input-group class="mb-3">
                  <b-input-group-prepend>
                    <b-input-group-text><i class="icon-lock"></i></b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input type="password" class="form-control" placeholder="Password" autocomplete="new-password"  name="password" v-model="password" v-validate="'required'" ref="password"/>
                </b-input-group>
                <div class="alert alert-danger" v-show="errors.has('password')">{{ errors.first('password')}}</div>

                <b-input-group class="mb-4">
                  <b-input-group-prepend>
                    <b-input-group-text><i class="icon-lock"></i></b-input-group-text>
                  </b-input-group-prepend>
                  <b-form-input type="password" data-vv-as="Repeat password" v-model="repeatPassword" class="form-control" placeholder="Repeat password" autocomplete="new-password" ref="repeatPassword" name="repeatPassword" v-validate="'required|confirmed:password'"/>
                </b-input-group>
                <div class="alert alert-danger" v-show="errors.has('repeatPassword')">{{ errors.first('repeatPassword')}}</div>
                <b-button variant="success" block @click="submit">Create Account</b-button>
              </b-form>
            </b-card-body>
          </b-card>
        </b-col>
      </b-row>
    </div>
    <!-- Modal -->
    <b-modal ref="myModalRef" hide-footer title="Verify your email" hide-header-close no-close-on-esc  no-close-on-backdrop>
      <div class="alert alert-success" role="alert">
        <h4 class="alert-heading">Good news!</h4>
        <p> You are almost done! We've just sent you a verification email. You will be able to sign in after you verify your email address. </p>
      </div>
    </b-modal>
  </div>
</template>

<script>

import VeeValidate from 'vee-validate';
import Vue from 'vue';

Vue.use(VeeValidate);

export default {
  name: 'Register',
  data:function(){
    return {
      email: '',
      password: '',
      repeatPassword: '',
      serverError: null
    }
  },
  methods: {
    submit() {
        this.$validator.validateAll().then((isValid) => {
          this.$data.serverError = null;
          if (!isValid)
            return false;
          var self = this;
          grecaptcha.execute('6Le1YnEUAAAAAL0o5kOVcKT8IPmEwdofEyk5zaUn').then(function(token) {
              Vue.axios.post(new URL('profile/register', process.env.VUE_APP_API_URL), {
                email: self.$data.email,
                password: self.$data.password,
                recaptcha: token
              }).then(
                () => { self.$refs.myModalRef.show(); },
                (error) => {
                  if (error.response.status == 400) {
                    if (Array.isArray(error.response.data)) {
                      self.$data.serverError = error.response.data[0];
                    } else self.$data.serverError = error.response.data;
                  }
                });

            });

      });
      return false;
    }
  }
};
</script>
